<template>
  <div id="app">
    <input
      type="date"
      v-model="start"
      @click.prevent="visible=true"
    >
    <input
      type="date"
      v-model="end"
      @click.prevent="visible=true"
    >
    <aki-date
      :custom="custom"
      :visible="visible"
      :selected="selected"
      :monday-first="mf"
      :select-area="sa"
      :single="single"
      @select="handlerSelect"
      @cancel="visible=false"
      @confirm="visible=false"
      @disable="dh"
      @viewport="vp"
    />
    <!-- <img alt="Vue logo" src="./assets/logo.png" />
    <HelloWorld msg="Welcome to Your Vue.js App" /> -->
  </div>
</template>

<script>
import { Snackbar } from 'clearaki-ui'
export default {
  name: 'App',
  data() {
    return {
      custom: {
        '2019-05-13': [{ bgc: 'green', color: 'gold', info: [{ text: 'x', color: 'orange' }, { text: 'xx', color: 'tomato' }, { text: 'xx', color: 'tomato' }] }, { bgc: 'blue', color: 'cyan', info: '123ABCasdsdasd', disabled: true }],
        // '2019-04': [{ bgc: 'blue', color: 'cyan', info: '123ABCasdsdasd', disabled: true }]
      },
      visible: true,
      selected: [],
      start: '',
      end: '',
      mf: false,
      sa: [],
      single: false
    }
  },
  created() {
    const vm = this
    setTimeout(() => {
      vm.custom = {
        '2019-05': [{ bgc: 'rgb(255,0,100)', color: 'gold', info: [{ text: 'x', color: 'orange' }, { text: 'xx', color: 'tomato' }] }],
        '2019-04': [{ bgc: 'h', color: 'cyan', info: '123ABCasdsdasd', disabled: true }, { info: ['001', '002'] }, { info: { text: '007', color: 'hsla(60, 50%, 70%, .6)' } }]
      }
      vm.sa = ['2018-10-20', '2019-11-15']
    }, 2000)
    setTimeout(() => {
      vm.selected = ['2019-04-20', '2019-05-12']
      
    }, 3000)
  },
  methods: {
    handlerSelect({ start, range, end }) {
      if (start && start.date)
        this.start = start.date.toYMD()
      if (end && end.date)
        this.end = end.date.toYMD()
    },
    dh(v) {
      Snackbar(`不能选择${v.date.toYMD()}`)
    },
    vp(v) {
      console.log('v', v)
    }
  }
}
</script>

